import React, { useState, useEffect } from "react";
import { Drawer, List, ListItem, ListItemButton, ListItemIcon, ListItemText, IconButton, Divider, Box, Typography } from "@mui/material";
import { Home, Dashboard, Menu } from "@mui/icons-material";
import StatusAlert from "./StatusAlert"; // ✅ Import StatusAlert

const Sidebar = ({ toggleDarkMode, darkMode }) => {
  const [open, setOpen] = useState(false);
  const [pm25, setPm25] = useState(0);

  useEffect(() => {
    const socket = new WebSocket("ws://localhost:8000");
    socket.onmessage = (event) => {
      const data = JSON.parse(event.data);
      if (data.pm25) setPm25(data.pm25); // ✅ Update PM2.5 value in real-time
    };

    return () => socket.close();
  }, []);

  const toggleDrawer = () => setOpen(!open);

  return (
    <>
      <IconButton onClick={toggleDrawer} sx={{ position: "absolute", top: 16, left: 16, color: "white" }}>
        <Menu />
      </IconButton>

      <Drawer
        anchor="left"
        open={open}
        onClose={toggleDrawer}
        PaperProps={{ sx: { width: 270, bgcolor: "background.default", color: "text.primary" } }}
      >
        {/* ✅ Prominent Status Alert Section */}
        <Box sx={{ p: 3, bgcolor: pm25 > 100 ? "error.main" : pm25 > 50 ? "warning.main" : "success.main", color: "white", textAlign: "center" }}>
          {/* <Typography variant="h5" fontWeight="bold">Air Quality Alert</Typography> */}
          <StatusAlert pm25={pm25} />
        </Box>

        <Divider />

        <List>
          <ListItem disablePadding>
            <ListItemButton href="/">
              <ListItemIcon><Home sx={{ color: "primary.main" }} /></ListItemIcon>
              <ListItemText primary="Home" />
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton href="/dashboard">
              <ListItemIcon><Dashboard sx={{ color: "primary.main" }} /></ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItemButton>
          </ListItem>
        </List>
      </Drawer>
    </>
  );
};

export default Sidebar;
