import React, { useState, useEffect } from "react";
import { Container, Typography, Grid, Paper, Box } from "@mui/material";
import StatusAlert from "../components/StatusAlert";
import LiveDataCards from "../components/LiveDataCards";
import LiveDataTable from "../components/LiveDataTable";
import LiveMap from "../components/LiveMap";

const API_URL = "http://localhost:8000/data";
const WS_URL = "ws://localhost:8000";

const Dashboard = () => {
  const [data, setData] = useState({});

  useEffect(() => {
    fetch(API_URL)
      .then((res) => res.json())
      .then((initialData) => {
        setData(initialData[initialData.length - 1]); // ✅ Load latest data
      });

    const socket = new WebSocket(WS_URL);
    socket.onmessage = (event) => {
      const newData = JSON.parse(event.data);
      setData(newData); // ✅ Update real-time data
    };

    return () => socket.close();
  }, []);

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      {/* ✅ Status Alert Section */}
      <Paper
        elevation={3}
        sx={{
          p: 3,
          mb: 4,
          borderRadius: 2,
          bgcolor: data.pm25 > 100 ? "error.main" : data.pm25 > 50 ? "warning.main" : "success.main",
          color: "white",
          textAlign: "center",
        }}
      >
        <Typography variant="h5" fontWeight="bold">Air Quality Alert</Typography>
        <StatusAlert pm25={data.pm25} />
      </Paper>

      {/* ✅ Dashboard Content */}
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <LiveDataCards data={data} />
        </Grid>
        <Grid item xs={12}>
          <LiveMap lat={data.lat} lon={data.lon} pm25={data.pm25} />
        </Grid>
        <Grid item xs={12}>
          <LiveDataTable data={[data]} />
        </Grid>
      </Grid>
    </Container>
  );
};

export default Dashboard;
