import React, { useEffect, useState } from "react";
import dynamic from "next/dynamic";
import { CircularProgress, Box, Typography } from "@mui/material";

// ✅ Dynamically import react-leaflet only on the client side
const MapContainer = dynamic(() => import("react-leaflet").then((mod) => mod.MapContainer), { ssr: false });
const TileLayer = dynamic(() => import("react-leaflet").then((mod) => mod.TileLayer), { ssr: false });
const Marker = dynamic(() => import("react-leaflet").then((mod) => mod.Marker), { ssr: false });
const Popup = dynamic(() => import("react-leaflet").then((mod) => mod.Popup), { ssr: false });

const LiveMap = ({ lat, lon, pm25 }) => {
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
    console.log("🔹 Map Received lat/lon:", lat, lon);
  }, [lat, lon]);

  if (!mounted) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" height={300}>
        <CircularProgress color="primary" />
      </Box>
    );
  }

  if (!lat || !lon) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" height={300}>
        <Typography variant="h6" color="error">⚠️ Location data not available</Typography>
      </Box>
    );
  }

  return (
    <MapContainer center={[lat, lon]} zoom={10} style={{ height: "300px", width: "100%" }}>
      <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
      <Marker position={[lat, lon]}>
        <Popup>PM2.5: {pm25} µg/m³</Popup>
      </Marker>
    </MapContainer>
  );
};

export default LiveMap;
