import React, { useState, useMemo } from "react";
import { ThemeProvider, createTheme, CssBaseline } from "@mui/material";
import Sidebar from "../components/Sidebar";
import "@fontsource/inter"; // ✅ Import Google Font


export default function MyApp({ Component, pageProps }) {
  const [darkMode, setDarkMode] = useState(false);

  const theme = useMemo(() =>
    createTheme({
      palette: {
        mode: darkMode ? "dark" : "light",
        primary: { main: "#007bff" },
        background: { default: darkMode ? "#121212" : "#f4f6f8", paper: darkMode ? "#1e1e1e" : "#fff" },
      },
      typography: {
        fontFamily: "Inter, sans-serif", // ✅ Updated Font
        h1: { fontSize: "2rem", fontWeight: "bold" }, // ✅ Adjusted sizes
        h2: { fontSize: "1.75rem", fontWeight: "bold" },
        h3: { fontSize: "1.5rem", fontWeight: "bold" },
        h4: { fontSize: "1.25rem", fontWeight: "600" },
        body1: { fontSize: "1rem" },
        body2: { fontSize: "0.9rem", color: "#666" },
      },
    }),
    [darkMode]
  );

  const toggleDarkMode = () => setDarkMode(!darkMode);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Sidebar toggleDarkMode={toggleDarkMode} darkMode={darkMode} />
      <Component {...pageProps} />
    </ThemeProvider>
  );
}
